@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Caveat:wght@600&family=Gochi+Hand&family=Kaushan+Script&family=Lato:wght@400;700&family=Noto+Sans:wght@100;400;500&family=Open+Sans:wght@300;400&family=Piazzolla:wght@200;300;400;600&family=Poppins&family=Roboto&family=Sedgwick+Ave+Display&family=Special+Elite&family=Ubuntu:wght@400;500&family=Urbanist:ital,wght@0,200;0,300;0,400;0,500;1,300&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Special Elite", serif;
}
.container {
  width: 100%;
  height: 100vh;
  background: #f5f5f5;
  /* border: 2px solid red; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.login-container {
  padding: 30px 5%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  background: #d1f2f7;
  flex-wrap: wrap;
  border-radius: 5px;
  box-shadow: rgba(27, 27, 27, 0.19) 0px 10px 20px,
    rgba(27, 27, 27, 0.23) 0px 6px 6px;
  margin: 20px;
}

.login-container .form-left {
  flex-basis: 48%;
  /* border: 2px solid red; */
  padding: 20px;
  padding-left: 5%;
  align-self: center;
  background: #232e32;
  border-radius: 8px;
  margin: 10px;
}

.form-left input {
  background: transparent;
  border: none;
  height: 25px;
  font-size: 16px;
  outline: none;
  margin-left: 10px;
  letter-spacing: 1.2px;
  color: #fff;
}
.form-left .input-div {
  padding: 8px 0px;
  /* border: 2px solid red; */
  border: 1px solid rgb(255, 255, 255);
  margin: 8px;
}
h1 {
  margin-bottom: 20px;
  margin-top: 15px;
  position: relative;
  display: block;
  color: #fff;
}
h1::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  background: #01b399;
  margin-bottom: 20px;
  top: -36px;
  left: 60px;
  border-top-left-radius: 45px;
  border-bottom-right-radius: 45px;
}
.login-container .img-right {
  flex-basis: 40%;
  /* border: 2px solid red; */
}
.img-right img {
  width: 100%;
}
input::placeholder {
  font-size: 14px;
}

.btn {
  padding: 12px 50px;
  border-radius: 8px;
  font-size: 16px;
  color: white;
  background: #01b399;
  border: none;
  /* margin-top: 25px; */
  cursor: pointer;
  /* margin-bottom: 10px; */
}
.btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* border: 1px solid red; */
  padding-inline: 8px;
  padding-top: 0;
  gap: 10px;
  margin-top: 20px;
}
.submit-btn {
  background: #d73107;
}

.reset-btn {
  background: white;
  color: #232e32;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.form-left a {
  color: #01b399;
}
.form-left p {
  font-size: 14px;
  margin-left: 5px;
}

.results {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  height: 70px;
  background: #232e32;
}

.left-cont {
  flex-basis: 50%;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.left-cont h2 {
  padding: 15px;
  background: #d73107;
  width: 88%;
  border-radius: 8px;
  margin: 10px;
  padding-bottom: 8px;
  padding-left: 30px;
  color: #fff;
}
.res {
  font-style: italic;
}
@media (max-width: 767px) {
  .container {
    height: 100%;
    width: auto;
  }
  .login-container {
    flex-wrap: wrap;
    flex-direction: column-reverse;
  }
  .login-container .form-left {
    flex-basis: 100%;
  }
  .login-container .img-right {
    display: none;
  }
  .left-cont h2 {
    width: 100%;
    font-size: 22px;
  }
}
